import { IImage } from 'api/interfaces'
import Icon from 'components/utils/Icon'
import ResponsiveImage from 'components/utils/ResponsiveImage'
import Carousel from 'nuka-carousel'
import * as React from 'react'
import { Button, ButtonProps, Label } from 'semantic-ui-react'
import { disableScroll, enableScroll, getViewPortSize, range } from 'utils/misc'

interface IProps {
  images: IImage[]
  carouselProps?: any
  slideIndex: number
  handleAfterSlide: (newSlideIndex: number) => any
  onClose: () => any
  open: boolean
}

interface ISlideButton {
  nextSlide: () => any
  previousSlide: () => any
  currentSlide: number
  slideCount: number
  goToSlide: (n: number) => any
}

const SlideButton = (props: ButtonProps) => (
  <Button
    color="black"
    style={{
      opacity: 0.6,
    }}
    {...props}
  />
)

export default class extends React.Component<IProps> {
  public render() {
    const { handleAfterSlide, slideIndex, open, images, carouselProps } = this.props
    const viewPortSize = getViewPortSize()
    const imageSize = {
      width: Math.floor(viewPortSize.width * .9),
      height: Math.floor(viewPortSize.height * .9),
    }
    if (!open) {
      enableScroll()
      return null
    }
    disableScroll()
    return (
      <div className="full-screen-modal">
        <Carousel
          style={{
            margin: 'auto',
          }}
          slideIndex={slideIndex}
          afterSlide={handleAfterSlide}
          initialSlideHeight={imageSize.height}
          renderTopRightControls={() => <Icon
            className="close-icon"
            icon="times"
            as="a"
            inverted={true}
            onClick={this.props.onClose}
          />}
          renderCenterLeftControls={({ previousSlide }: ISlideButton) =>
            <SlideButton
              content={<Icon icon="chevron-double-left" />}
              onClick={previousSlide}
              style={{ paddingLeft: 10 }}
            />}
          renderCenterRightControls={({ nextSlide }: ISlideButton) =>
            <SlideButton
              content={<Icon className="no-margin" icon="chevron-double-right" />}
              style={{ paddingRight: 10 }}
              onClick={nextSlide}
            />}
          renderBottomCenterControls={({ currentSlide, slideCount, goToSlide }: ISlideButton) => {
            const labels = range(0, slideCount - 1)
            return labels.map(
              (k) => (
                <Label
                  key={k}
                  circular={true}
                  empty={true}
                  color={currentSlide === k ? 'red' : 'grey'}
                  onClick={() => goToSlide(k)}
                />
              ),
            )
          }}

          wrapAround={true}
          {...carouselProps}
        >
          {images
            .map(
              ({ public_id }, i) => {
                if (
                  i <= slideIndex + 2
                ) {
                  return (
                    <ResponsiveImage
                      onLoad={(e) => {
                        const target = e.currentTarget
                        const { height, width } = imageSize
                        if (target.height > height) {
                          target.style.height = `${height}px`
                          target.style.width = 'auto'
                        } else {
                          target.style.width = `${width}px`
                          target.style.height = 'auto'
                        }
                        target.style.margin = 'auto'
                        setTimeout(
                          () => {
                            window.dispatchEvent(new Event('resize'))
                          },
                          25,
                        )
                      }}
                      key={public_id}
                      publicId={public_id}
                      semanticProps={{
                        fluid: true,
                      }}
                      cloudinaryProps={{
                        ...imageSize,
                        crop: 'limit',
                        style: {
                          height: '100%',
                        },
                      }}
                    />
                  )
                }
                return <div key={public_id} />
              },
          )}
        </Carousel>
      </div>
    )
  }
}



